import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { signUp } from '../../api/user';
import './style.css'

export const SignUp = ({ switchLogin }) => {
    const [username, setUsername] = useState("");
    const [name, setName] = useState("");
    const [password, setPassword] = useState("");
    const [address, setAddresss] = useState("");
    const [email, setEmail] = useState("");
    const [repassword, setRepassword] = useState("");
    const navigate = useNavigate();

    const validateEmail = (email) => {
        return String(email)
            .toLowerCase()
            .match(
                /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
            );
    };

    const validatePassword = (password, repassword) => {
        return password === repassword;
    }

    const handleSignUp = async () => {
        if (!username)
        {
            alert("Vui lng nhp tn ng nhp!");
            return;
        }
        if (!password)
        {
            alert("Vui lng nhp mt khu!");
            return;
        }
        if (!name)
        {
            alert("Vui lng nhp tn!");
            return
        }
        if (!address)
        {
            alert("Vui lng nhp a ch!");
            return
        }

        if (!email)
        {
            alert("Vui lng nhp email!");
            return
        }
        if (!repassword)
        {
            alert("Vui lng nhp li mt khu!");
            return
        }

        if (!validateEmail(email))
        {
            alert("Vui lng kim tra li nh dng email!");
            return
        }
        if (!validatePassword(password, repassword))
        {
            alert("Mt khu nhp li khng khp!");
            return
        }

        const status = await signUp(username, password, name, email, address);
        //console.log(status);
        if (status)
        {
            switchLogin();
        }
        else alert('ng k tht bi!');
    }

    return (
        <div className='signup'>
            <strong>Tn ng nhp</strong>
            <input
                type="text" name="username" id="username"
                onChange={e => setUsername(e.target.value)}
                placeholder="Nhp tn ng nhp" />
            <strong>H v tn</strong>
            <input type="text" name="name" id="name"
                onChange={e => setName(e.target.value)}
                placeholder="Nhp h v tn" />
            <strong>Email</strong>
            <input type="email" name="email" id="email"
                onChange={e => setEmail(e.target.value)}
                placeholder="Nhp email" />
            <strong>a ch</strong>
            <input type="text" name="address" id="address"
                onChange={e => setAddresss(e.target.value)}
                placeholder="Nhp a ch" />
            <strong>Mt khu</strong>
            <input type="password" name="password" id="password"
                onChange={e => setPassword(e.target.value)}
                placeholder="Nhp mt khu" />
            <input type="password" name="repassword" id="repassword"
                onChange={e => setRepassword(e.target.value)}
                placeholder="Nhp li mt khu" />
            <button className='signup-btn' onClick={handleSignUp}>ng k</button>
        </div>
    )
}